.footer{
    width:100%;
    padding:6rem 0;
    background-color:rgba(19,19,19,0.8);

}

.footer-container{
    max-width:1140px;
    margin:auto;
    display:grid;
    grid-template-columns:repeat(2,1fr);
grid-gap:40px;
}

.left{
    height:100%;
    max-width:100%;
    display:flex;
    flex-direction:column;
    padding:1rem;
    /* margin-right:1rem; */
}
.location,
.marker,
.phone,
.email{
    margin-bottom: 1rem;
}

.location{
  display:flex;
  margin-bottom:  0.8rem;
}

.location p{
    line-height:30px;

}

.right{
     height:100%;
     max-width:100%;
     display: flex;
     flex-direction:column;
     padding:1rem;
     /* margin-left:15rem; */
     

}

.social{
    margin-top:1rem;
}

h4{
    font-size:1.2rem;
    padding-bottom: 0.7rem;
}

p{
    font-size:1.2rem;
}
.marker{
    display:flex;
    align-items:center;
}

.icon-container{
    display: inline-flex;
}

.location-text{
    margin:0;
}

@media (max-width: 640px) {
    .footer-container {
      grid-template-columns: 1fr;
    }
   
  }
  
