@import url('https://fonts.googleapis.com/css2?family=Itim&family=Outfit:wght@100;200;300;400;500;600;700;800;900&family=Ubuntu&display=swap');

*{
  box-sizing:border-box;
  margin:0;
  padding:0;
  font-family: 'Outfit', sans-serif;
}

body{
  background:black;

}

h1,h4,h2,p,a{
  color: white;
  text-decoration:none;

}

ul{
  list-style-type:none;
}

.btn{
  padding:12px 32px;
  font-size:1rem;
  text-transform: uppercase;
  background: rgb(248,217,15);
  color:#222;
  border:1px dolid #fff;
  font-weight:600;
  cursor:pointer;
}

.btn-light{
  background:transparent;
  color:#fff;
}

.btn:hover{
  background:rgba(255,255,255,0.2);
  color:#fff;
  transition:0.3s;
  }